import React, { useCallback, useRef, useState } from "react";

import LogTable from "./components/LogTable";
import "antd/dist/antd.css";
import LogPlayer from "./components/LogPlayer";
import ReactPlayer from "react-player";
import { Radio, RadioChangeEvent, Switch } from "antd";

function App() {
  const [linked, setLinked] = useState<boolean>(true);
  const [video, setVideo] = useState<string>("");
  const [sync, setSync] = useState<number>(1629722777027);
  const [sec, setSec] = useState<number>(0);
  const [start, setStart] = useState<number>(29257);
  const [playedSec, setPlayedSec] = useState(0);
  const [selectedTimestamp, setSelectedTimestamp] = useState<number>(0);
  const playerRef = useRef() as React.LegacyRef<ReactPlayer> | undefined;

  // const onClickSync = useCallback(() => {
  //   setSync(selectedTimestamp);
  //   setStart(playedSec);
  // }, [playedSec, selectedTimestamp, setStart, setSync]);

  const onChangeVideo = useCallback((e: RadioChangeEvent) => {
    setVideo(e.target.value);
    if (linked) {
      if (e.target.value === "7") {
        setStart(29257);
        setSync(1629722777027);
      } else if (e.target.value === "8") {
        setStart(27275);
        setSync(1629723368224);
      } else if (e.target.value === "9") {
        setStart(28561);
        setSync(1629724008156);
      } else if (e.target.value === "10") {
        setStart(28977);
        setSync(1629729295883);
      } else if (e.target.value === "11") {
        setStart(26999);
        setSync(1629729867290);
      } else if (e.target.value === "12") {
        setStart(37468);
        setSync(1629730415581);
      }
    }
  }, [linked]);

  return (
    <div className="flex h-screen overflow-hidden">
      <div className="w-3/4 h-screen">
        <LogTable
          fileName="raw.csv"
          sec={linked ? sec : 0}
          sync={sync}
          setSelectedTimestamp={setSelectedTimestamp}
          playerRef={linked && playerRef}
          setSync={setSync}
          selectedTimestamp={selectedTimestamp}
          start={start}
          linked={linked}
        />
      </div>
      <div className="flex flex-col w-1/4 h-screen items-center">
        <div className="flex justify-center h-8 items-center pt-2">
          <div className="mr-2">Link:</div>
          <Switch checked={linked} onChange={(checked) => setLinked(checked)} />
          <div className="mx-2"></div>
          <Radio.Group
            defaultValue="7"
            size="small"
            buttonStyle="solid"
            onChange={onChangeVideo}
            value={video}
          >
            <Radio.Button value="7">7</Radio.Button>
            <Radio.Button value="8">8</Radio.Button>
            <Radio.Button value="9">9</Radio.Button>
            <Radio.Button value="10">10</Radio.Button>
            <Radio.Button value="11">11</Radio.Button>
            <Radio.Button value="12">12</Radio.Button>
          </Radio.Group>
          {/* <div className="mx-2"></div>
          <Button onClick={onClickSync} size="small">
            Sync
          </Button> */}
        </div>
        {/* <div>
          start: {start}, sync: {sync}
        </div> */}
        <LogPlayer
          url={`${
            process.env.NODE_ENV === "development" ? "abc-analyzer/" : ""
          }videos/${video}.webm`}
          setSec={setSec}
          setSync={setSync}
          setStart={setStart}
          setPlayedSec={setPlayedSec}
          selectedTimestamp={selectedTimestamp}
          playerRef={playerRef}
          playedSec={playedSec}
        />
      </div>
    </div>
  );
}

export default App;
